import React, { useState, useEffect } from 'react';

interface Ticket {
    id: string,
    name: string;
    status: string;
}

function Support() {
    const [serverCount, setServerCount] = useState<number>(0);
    const [servers, setServers] = useState<Array<Ticket>>();
    const [modalActive, setModalActive] = useState<boolean>(false);

    function openModal() {
        setModalActive(true)
    }

    function closeModal() {
        setModalActive(false)
    }

    useEffect(() => {
        let newServers: Array<Ticket> = [{
            id: "hej",
            name: "ClubCoding",
            status: "open",
        }, {
            id: "hej",
            name: "Course",
            status: "closed",
        },
        {
            id: "hej",
            name: "Högskoleprovskursen",
            status: "closed",
        }];
        setServers(newServers);
        if (newServers !== undefined) {
            setServerCount(newServers?.length);
        }
    }, []);
    return (
        <main className="container mx-auto px-32 pt-4">
            <div className="flex flex-wrap p-4">
                <div className="w-1/3 h-12">
                    <h2 className="text-3xl font-thin">Active tickets</h2>
                </div>
                <div className="w-1/3"></div>
                <div className="w-1/3 h-12 text-right">
                    <button onClick={openModal} className="bg-blue-500 text-white font-bold py-2 px-4 rounded hover:text-gray-900 hover:bg-blue-700">
                        Create new ticket
                </button>
                </div>
            </div>
            <div className="flex flex-wrap px-4 pb-4">
                {serverCount === 0 ? (
                    <div className="w-full">
                        <div className="card p-4 bg-white shadow flex">
                            <div className="flex-grow">
                                <div className="flex">
                                    <div>
                                        <h2 className="text-gray-700 text-xl font-thin">You currently have no servers.</h2>
                                        <h2 className="text-gray-900 text-l">Why don't you create one?</h2>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                ) : (
                        <div className="w-full">
                            {servers?.map((server, index) => {
                                if (index % 2 === 0) {
                                    return (
                                        <a href={"/tickets/" + server.id} className="card p-4 bg-white shadow flex">
                                            <div className="flex-grow">
                                                <div className="flex">
                                                    <svg className="m-1" xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M0 0h24v24H0z" fill="none" /><path d="M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm1 17h-2v-2h2v2zm2.07-7.75l-.9.92C13.45 12.9 13 13.5 13 15h-2v-.5c0-1.1.45-2.1 1.17-2.83l1.24-1.26c.37-.36.59-.86.59-1.41 0-1.1-.9-2-2-2s-2 .9-2 2H8c0-2.21 1.79-4 4-4s4 1.79 4 4c0 .88-.36 1.68-.93 2.25z" /></svg>
                                                    <div>
                                                        <a href={"/tickets/" + server.id} className="text-gray-700 text-xl">{server.name}</a>
                                                    </div>
                                                </div>
                                            </div>
                                            <div className="content-center flex-wrap my-2">
                                                <button className="bg-blue-500 text-gray-800 py-1 px-4 rounded-full text-sm font-normal">{server.status}</button>
                                            </div>
                                        </a>
                                    )
                                } else {
                                    return (
                                        <a href={"/servers/" + server.id} className="card p-4 bg-gray-100 shadow flex">
                                            <div className="flex-grow">
                                                <div className="flex">
                                                    <svg className="m-1" xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M0 0h24v24H0z" fill="none" /><path d="M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm1 17h-2v-2h2v2zm2.07-7.75l-.9.92C13.45 12.9 13 13.5 13 15h-2v-.5c0-1.1.45-2.1 1.17-2.83l1.24-1.26c.37-.36.59-.86.59-1.41 0-1.1-.9-2-2-2s-2 .9-2 2H8c0-2.21 1.79-4 4-4s4 1.79 4 4c0 .88-.36 1.68-.93 2.25z" /></svg>
                                                    <div>
                                                        <a href={server.id} className="text-gray-700 text-xl">{server.name}</a>
                                                    </div>
                                                </div>
                                            </div>
                                            <div className="content-center flex-wrap my-2">
                                                <button className="bg-blue-500 text-gray-800 py-1 px-4 rounded-full text-sm font-normal">{server.status}</button>
                                            </div>
                                        </a>
                                    )
                                }
                            })}
                        </div>
                    )
                }

            </div>
            <div className={`modal fixed w-full h-full top-0 left-0 flex items-center justify-center ${modalActive ? "" : "opacity-0 pointer-events-none"}`}>
                <div onClick={closeModal} className="modal-overlay absolute w-full h-full bg-gray-900 opacity-50"></div>

                <div className="modal-container bg-white w-11/12 md:max-w-md mx-auto rounded shadow-lg z-50 overflow-y-auto">

                    <div className="modal-content py-4 text-left">
                        <p className="text-2xl text-center">Create new Server</p>
                        <hr className="my-2 mx-32"></hr>
                        <form className="px-8 pb-2 mb-4">
                            <div className="mb-4">
                                <label className="block text-gray-700 text-sm font-bold mb-2" htmlFor="name">
                                    Title
                                </label>
                                <input className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="name" type="text" placeholder="Title" />
                            </div>
                            <div className="mb-4">
                                <label className="block text-gray-700 text-sm font-bold mb-2" htmlFor="text">
                                    Text
                                </label>
                                <textarea className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" placeholder="Text"></textarea>
                            </div>
                            <div className="flex items-center justify-between">
                                <button className="w-full bg-blue-500 hover:bg-blue-700 text-white font-bold p-3 rounded focus:outline-none focus:shadow-outline" type="button">
                                    Create ticket
                                </button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </main>
    );
}

export default Support;