import React, { useState, useEffect } from 'react';

interface Server {
    id: string,
    name: string;
    url: string;
    status: string;
}

function Home() {
    const [modalActive, setModalActive] = useState<boolean>(false);
    const [useCustomDomain, setUseCustomDomain] = useState<boolean>(false);
    const [serverCount, setServerCount] = useState<number>(0);
    const [servers, setServers] = useState<Array<Server>>();


    function updateUseCustomDomain() {
        setUseCustomDomain(!useCustomDomain)
    }
    function openModal() {
        setModalActive(true)
    }

    function closeModal() {
        setModalActive(false)
    }

    useEffect(() => {
        let newServers: Array<Server> = [{
            id: "0",
            name: "ClubCoding",
            url: "clubcoding.com",
            status: "running",
        }, {
            id: "0",
            name: "Course",
            url: "course.ocp-host.com",
            status: "running",
        },
        {
            id: "0",
            name: "Högskoleprovskursen",
            url: "hogskoleprovskursen.se",
            status: "running",
        }];
        setServers(newServers);
        if (newServers !== undefined) {
            setServerCount(newServers?.length);
        }
    }, []);
    return (
        <main className="container mx-auto px-32 pt-4">
            <div className="flex flex-wrap p-4">
                <div className="w-1/3 h-12">
                    <h2 className="text-3xl font-thin">Active servers</h2>
                </div>
                <div className="w-1/3"></div>
                <div className="w-1/3 h-12 text-right">
                    <button onClick={openModal} className="bg-blue-500 text-white font-bold py-2 px-4 rounded hover:text-gray-900 hover:bg-blue-700">
                        Create new Server
                </button>
                </div>
            </div>
            <div className="flex flex-wrap px-4 pb-4">
                {serverCount === 0 ? (
                    <div className="w-full">
                        <div className="card p-4 bg-white shadow flex">
                            <div className="flex-grow">
                                <div className="flex">
                                    <div>
                                        <h2 className="text-gray-700 text-xl font-thin">You currently have no servers.</h2>
                                        <h2 className="text-gray-900 text-l">Why don't you create one?</h2>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                ) : (
                        <div className="w-full">
                            {servers?.map((server, index) => {
                                if (index % 2 === 0) {
                                    return (
                                        <a href={"/servers/" + server.id} className="card p-4 bg-white shadow flex">
                                            <div className="flex-grow">
                                                <div className="flex">
                                                    <svg className="m-1" width="24" height="24" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path d="M0 0h24v24H0z" fill="none" /><path d="M20 13H4c-.55 0-1 .45-1 1v6c0 .55.45 1 1 1h16c.55 0 1-.45 1-1v-6c0-.55-.45-1-1-1zM7 19c-1.1 0-2-.9-2-2s.9-2 2-2 2 .9 2 2-.9 2-2 2zM20 3H4c-.55 0-1 .45-1 1v6c0 .55.45 1 1 1h16c.55 0 1-.45 1-1V4c0-.55-.45-1-1-1zM7 9c-1.1 0-2-.9-2-2s.9-2 2-2 2 .9 2 2-.9 2-2 2z" /></svg>
                                                    <div>
                                                        <h2 className="text-gray-700 text-xl">{server.name}</h2>
                                                        <a href={"http://" + server.url} className="text-gray-700 text-sm">{server.url}</a>
                                                    </div>
                                                </div>
                                            </div>
                                            <div className="content-center flex-wrap my-2">
                                                <button className="bg-blue-500 text-gray-800 py-1 px-4 rounded-full text-sm font-normal">{server.status}</button>
                                            </div>
                                        </a>
                                    )
                                } else {
                                    return (
                                        <a href={"/servers/" + server.id} className="card p-4 bg-gray-100 shadow flex">
                                            <div className="flex-grow">
                                                <div className="flex">
                                                    <svg className="m-1" xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M0 0h24v24H0z" fill="none" /><path d="M20 13H4c-.55 0-1 .45-1 1v6c0 .55.45 1 1 1h16c.55 0 1-.45 1-1v-6c0-.55-.45-1-1-1zM7 19c-1.1 0-2-.9-2-2s.9-2 2-2 2 .9 2 2-.9 2-2 2zM20 3H4c-.55 0-1 .45-1 1v6c0 .55.45 1 1 1h16c.55 0 1-.45 1-1V4c0-.55-.45-1-1-1zM7 9c-1.1 0-2-.9-2-2s.9-2 2-2 2 .9 2 2-.9 2-2 2z" /></svg>
                                                    <div>
                                                        <h2 className="text-gray-700 text-xl">{server.name}</h2>
                                                        <a href={"http://" + server.url} className="text-gray-700 text-sm">{server.url}</a>
                                                    </div>
                                                </div>
                                            </div>
                                            <div className="content-center flex-wrap my-2">
                                                <button className="bg-blue-500 text-gray-800 py-1 px-4 rounded-full text-sm font-normal">{server.status}</button>
                                            </div>
                                        </a>
                                    )
                                }
                            })}
                        </div>
                    )
                }

            </div>
            <div className={`modal fixed w-full h-full top-0 left-0 flex items-center justify-center ${modalActive ? "" : "opacity-0 pointer-events-none"}`}>
                <div onClick={closeModal} className="modal-overlay absolute w-full h-full bg-gray-900 opacity-50"></div>

                <div className="modal-container bg-white w-11/12 md:max-w-md mx-auto rounded shadow-lg z-50 overflow-y-auto">

                    <div className="modal-content py-4 text-left">
                        <p className="text-2xl text-center">Create new Server</p>
                        <hr className="my-2 mx-32"></hr>
                        <form className="px-8 pb-2 mb-4">
                            <div className="mb-4">
                                <label className="block text-gray-700 text-sm font-bold mb-2" htmlFor="name">
                                    Website Name
                                </label>
                                <input className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="name" type="text" placeholder="Website Name" />
                            </div>
                            <div className="mb-4">
                                <label className="block text-gray-700 text-sm font-bold mb-2" htmlFor="username">
                                    Admin Username
                                </label>
                                <input className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="username" type="text" placeholder="Admin Username" />
                            </div>
                            <div className="mb-4">
                                <label className="block text-gray-700 text-sm font-bold mb-2" htmlFor="password">
                                    Admin Password
                                </label>
                                <input className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="password" type="password" placeholder="Admin Password" />
                            </div>
                            <div className="mb-4">
                                <div className="md:w-1/3"></div>
                                <label className="md:w-2/3 block text-gray-700 font-bold">
                                    <input className="mr-2 leading-tight" type="checkbox" defaultChecked={useCustomDomain} onChange={updateUseCustomDomain} />
                                    <span className="text-sm">
                                        Use a custom domain
                                    </span>
                                </label>
                            </div>
                            {useCustomDomain &&
                                <div className="mb-4">
                                    <label className="block text-gray-700 text-sm font-bold mb-2" htmlFor="domain">
                                        Custom Domain
                                    </label>
                                    <input className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="domain" type="text" placeholder="Custom Domain" />
                                </div>
                            }
                            <div className="flex items-center justify-between">
                                <button className="w-full bg-blue-500 hover:bg-blue-700 text-white font-bold p-3 rounded focus:outline-none focus:shadow-outline" type="button">
                                    Create server
                                </button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </main>
    );
}

export default Home;