import React, { useState, useEffect } from 'react';
import Home from "./Home";
import Support from "./Support";
import Server from "./Server";
import Ticket from "./Ticket";

import { Route, Switch } from 'react-router-dom';

interface Server {
  id: string,
  name: string;
  url: string;
  status: string;
}


function App() {
  const [serverCount, setServerCount] = useState<number>(0);
  const [ticketsCount, setTicketsCount] = useState<number>(0);
  const [dropdownActive, setDropdownActive] = useState<boolean>(false);

  function changeDropdown() {
    setDropdownActive(!dropdownActive)
  }

  useEffect(() => {
    let newServers: Array<any> = [{
      id: 0,
      name: "ClubCoding",
      url: "clubcoding.com",
      status: "running",
    }, {
      id: 0,
      name: "Course",
      url: "course.ocp-host.com",
      status: "running",
    },
    {
      id: 0,
      name: "Högskoleprovskursen",
      url: "hogskoleprovskursen.se",
      status: "running",
    }];
    if (newServers !== undefined) {
      setServerCount(newServers?.length);
    }
    setTicketsCount(0);
  }, []);

  return (
    <div className="App">
      <nav className="container mx-auto px-32">
        <div className="flex items-center justify-between flex-wrap p-6">
          <div className="flex items-center flex-shrink-0 mr-6">
            <svg className="text-orange-600 fill-current h-8 w-8 mr-2" width="24" height="24" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path d="M0 0h24v24H0z" fill="none" /><path d="M19.35 10.04C18.67 6.59 15.64 4 12 4 9.11 4 6.6 5.64 5.35 8.04 2.34 8.36 0 10.91 0 14c0 3.31 2.69 6 6 6h13c2.76 0 5-2.24 5-5 0-2.64-2.05-4.78-4.65-4.96zM14 13v4h-4v-4H7l5-5 5 5h-3z" /></svg>
            <span className="font-semibold text-xl tracking-tight">OpenCourseWare <span className="text-orange-600 font-bold">Host</span></span>
          </div>
          <div className="block lg:hidden">
            <button className="flex items-center px-3 py-2 border rounded text-gray-700 border-teal-400 hover:border-white">
              <svg className="fill-current h-3 w-3" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><title>Menu</title><path d="M0 3h20v2H0V3zm0 6h20v2H0V9zm0 6h20v2H0v-2z" /></svg>
            </button>
          </div>
          <div className="w-full block flex-grow lg:flex lg:items-center lg:w-auto">
            <div className="lg:flex-grow">
            </div>
            <div>
              <a href="/" className="text-gray-600 inline-block text-sm px-4 py-2 leading-none border rounded  border-white hover:border-transparent hover:text-gray-900 mt-4 lg:mt-0">
                Servers <span className="bg-orange-700 p-1 rounded text-white text-xs ml-1">{serverCount}</span>
              </a>
              <a href="/support" className="text-gray-600 inline-block text-sm px-4 py-2 leading-none border rounded  border-white hover:border-transparent hover:text-gray-900 mt-4 lg:mt-0">
                Support <span className="bg-orange-700 p-1 rounded text-white text-xs ml-1">{ticketsCount}</span>
              </a>
              <a href="/support" className="text-gray-600 inline-block text-sm px-4 py-2 leading-none border rounded  border-white hover:border-transparent hover:text-gray-900 mt-4 lg:mt-0">
                Log out
              </a>
            </div>
          </div>
        </div>
      </nav>
      <hr />
      <div className="h-screen w-full bg-gray-100">
        <Switch>
          <Route exact path="/" render={() => (
            <Home />
          )} />
          <Route exact path="/support" render={() => (
            <Support />
          )} />
          <Route exact path="/servers/:id" render={() => (
            <Server />
          )} />
          <Route exact path="/tickets/:id" render={() => (
            <Ticket />
          )} />
        </Switch>
        <footer className="text-center text-gray-700">
          &copy; 2019 OpenCourseWare Host
        </footer>
      </div>
    </div>
  );
}

export default App;
